
import util from '@/libs/util'
import record from './record.mixin'
import wxjssdk from '@/libs/wxjssdk.mixin'
import common from '@/libs/common.mixin'
import FileItem from 'c%/FileItem.vue'
import SelectFile from 'c%/SelectFile.vue'
import Tag from 'c%/Tag.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Rater from 'vux/src/components/rater/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'


const emptyObject = {
hc_id: 0,
eid: 0,
content: '',
result_level: 0,
check_level: 2,
homework_attachment: []
}

export default{
mixins: [common,record,wxjssdk],
components: {
Tag,
Tab,
Cell,
Rater,
Group,
XButton,
TabItem,
XTextarea,
Flexbox,
FileItem,
FlexboxItem,
SelectFile
},
props: {
mode: {
type: String,
default: 'add'
},
hcId: {
type: [String,Number],
default: 0
},
homework: {
type: Object,
default: ()=>{
return {}
}
}
},
data () {
return {
tab_index: 0,
tab_list: ['精批','普批'],
standard_levels: [
{id:10,name: 'A+'},
{id:9,name: 'A'},
{id:8,name: 'A-'},
{id:7,name: 'B+'},
{id:6,name: 'B'},
{id:5,name: 'B-'},
{id:4,name: 'C+'},
{id:3,name: 'C'},
{id:2,name: 'C-'},
{id:1,name: 'D'}
],
normal_levels: [
{id:4,name: 'A'},
{id:3,name: 'B'},
{id:2,name: 'C'},
{id:1,name: 'D'}
],
info: util.copy(emptyObject),
showRecord: false
}
},
methods: {
publish () {
this.$emit('on-publish')
},
addToArtwork () {
this.$emit('add-artwork')
},
submit_correct () {
this.info.check_level = this.tab_index + 1
this.info.hc_id = this.hcId
this.info.eid = this.eid$
this.info.bid = this.homework.bid
this.showLoading()

this.$rest('homework_completes/'+this.hcId+'/homework_reply')
.post(this.info)
.success(data=>{
this.hideLoading()
this.toast('提交成功','success')
this.$emit('on-success')
this.cancel()
}).error(response=>{
this.hideLoading()
this.toast(response.body.message||'提交失败','warn')
})
},
cancel () {
this.$emit('close')
this.info = util.copy(emptyObject)
}
},
computed: {
level_img () {
let [levels_list,homework,img,obj] = [this.standard_levels,this.homework,'https://sp1.xiao360.com/static/img/assets/images/A.png',{}]
if(homework.is_check===1){
if(homework.check_level==2){
levels_list = this.normal_levels
}

obj = levels_list.find(l=>l.id==homework.result_level)

if(obj){
img = obj.name
}
}
return `https://sp1.xiao360.com/static/img/neza/org/${img}.png`
},
disabled () {
return this.info.result_level===0||this.info.content.length===0
},
levels () {
let [data,standard_levels,normal_levels] = [this.data,this.standard_levels,this.normal_levels]

if(this.tab_index == 0){
return standard_levels
}
return normal_levels
}
}
}



