import util from '@/libs/util'

export default{
	data () {
		return {
			recording: false,
			time: 0,
			time_out: null
		}
	},
	mounted () {
		
	},
	methods: {
		closeRecordMask () {
			if(!this.recording){
				this.showRecord = false
			}
		},
		record () {
			this.recording = !this.recording
			 
			if(this.recording){				
				this.startRecordVoice()
			}else{				
				this.stopRecordVoice()
			}
		},
		onVoiceAutoEnd () {
			this.onVoiceRecordEnd()
			.then(item=>{
				this.recording = false
				let obj = {
					file_id: item.file_id,
					file_type: 'voice',
					file_url: item.file_url,
					duration: util.copy(this.time)
				}
				
				this.info.homework_attachment.push(obj)
				this.resetTime()
				this.showRecord = false
			})
		},
		startRecordVoice () {
			this.recording = true
			
			this.onVoiceAutoEnd()
			
			this.startRecord()
			.then(()=>{
				this.time_out = setTimeout(this.timeCounting, 1000)					
			})
		},
		stopRecordVoice () {
			this.recording = false
			
			this.stopRecord()
			.then(item=>{				
				let obj = {
					file_id: item.file_id,
					file_type: 'voice',
					file_url: item.file_url,
					duration: util.copy(this.time)
				}
				
				this.info.homework_attachment.push(obj)
				this.resetTime()
			})
			this.showRecord = false
		},
		resetTime () {
			this.time = 0
			
			if(this.time_out) window.clearTimeout(this.time_out)
				
			if(this.setRecordTime) window.clearTimeout(this.setRecordTime)
		},
		timeCounting () {
			this.time = 0
    		this.setRecordTime()
		},
		setRecordTime () {
			if(this.time < 60 && this.recording){
				this.time ++
				window.setTimeout(this.setRecordTime,1000)				
			}
		}
	},
	computed: {
		recordTime () {
			let time = this.time
			let result = 0+':'+ (time<10?('0'+time):time)
			
			if(time == 60){
				result = '1:00'
			}
			
			return result
		}
	}
	
}



